import AbstractView from './../abstract-view';

class WelcomView extends AbstractView {

  get template() {
    return `
      <section class="greeting central--blur">
      <img class="greeting__logo" src="img/logo_ph-big.svg" width="201" height="89" alt="Pixel Hunter">
      <div class="greeting__asterisk asterisk"><span class="visually-hidden">   </span>*</div>
      <div class="greeting__challenge">
        <h3 class="greeting__challenge-title"> -   !</h3>
        <p class="greeting__challenge-text">  :</p>
        <ul class="greeting__challenge-list">
          <li>       .</li>
          <li>  ,   ,    .</li>
          <li>   .</li>
          <li>,     .</li>
        </ul>
      </div>
      <button class="greeting__continue" type="button">
        <span class="visually-hidden"></span>
        <svg class="icon" width="64" height="64" viewBox="0 0 64 64" fill="#000000">
          <use xlink:href="img/sprite.svg#arrow-right"></use>
        </svg>
      </button>
    </section>`;
  }

  bind() {
    const continueButton = this.element.querySelector(`.greeting__continue`);
    continueButton.addEventListener(`click`, (evt) => {
      evt.preventDefault();

      this.onClick();
    });
  }

  onClick() {}
}

export default WelcomView;
