import AbstractView from './../abstract-view';

class RulesView extends AbstractView {

  get template() {
    return `
      <section class="rules">
        <h2 class="rules__title"></h2>
        <ul class="rules__description">
          <li> 10     
            <img class="rules__icon" src="img/icon-photo.png" width="32" height="31" alt="">  
            <img class="rules__icon" src="img/icon-paint.png" width="32" height="31" alt=""></li>
          <li>      .</li>
          <li>    30 .</li>
          <li>    3 .</li>
        </ul>
        <p class="rules__ready">?</p>
        <form class="rules__form">
          <input class="rules__input" type="text" placeholder=" ">
          <button class="rules__button  continue" type="submit" disabled>Go!</button>
        </form>
      </section>`;
  }

  bind() {
    const continueButton = this.element.querySelector(`.rules__button`);
    const nameField = this.element.querySelector(`.rules__input`);

    nameField.addEventListener(`input`, () => {
      continueButton.disabled = nameField.value.length > 0 ? false : true;
    });

    continueButton.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      const playerName = nameField.value;
      this.onClick(playerName);
    });
  }

  onClick() {}
}

export default RulesView;
